/* =====================
   Global Variables
===================== */
:root {
  --PrimaryColor: hsl(26, 93%, 50%);
  --SecondaryColor: hsl(200, 90%, 50%);
  --AccentColor: hsl(340, 82%, 52%);

  --White: #fff;
  --Black: #222;
  --Text: #444;
  --LightGray: #f8f9fa;
  --DarkGray: #555;
}

/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: var(--Text);
  background: var(--White);
}

/* =====================
   Navbar Fix
===================== */
.navBarSection .header {
  position: fixed;
  top: 0;
  width: 100%;
  background: var(--White);
  border-bottom: 1px solid #eee;
  z-index: 1000;
}

.logoDiv .logo h1 {
  color: var(--PrimaryColor); /* Dot color fixed */
}

/* =====================
   Hero Section
===================== */
